export default {
	data() {
		return {
			userIsLogin: false,
			userInfo:{}
		}
	},
	methods: {
		jump_meinfo(){
			uni.navigateTo({
				url: "../meInfo/meInfo"
			});
		}
	},
	onShow() {
		var me = this;
		//用户状态的切换
		// if(userinfo != null && userinfo != undefined && userinfo != ""){
		// 	me.userIsLogin = true;
		// 	me.userInfo = userinfo;
		// }else{
		// 	me.userIsLogin = false;
		// 	me.userInfo = {};
		// }
		// 使用挂载方法获取用户数据
		var userInfo = me.getGlobalUser("globalUser");
		if(userInfo){
			me.userIsLogin = true;
			me.userInfo = userInfo;
		}else{
			me.userIsLogin = false;
			me.userInfo = {};
		}
	}
}
